import React from 'react'
import { Container, Flex, Box } from 'theme-ui'
import ContentText from '@solid-ui-components/ContentText'
import Divider from '@solid-ui-components/Divider'
import ProgressBar from '@solid-ui-components/ProgressBar'
import Counter from '@solid-ui-components/Counter'
import StackTitle from '@solid-ui-components/StackTitle'
import FlexImage from '@solid-ui-components/FlexImage'
import FlexContent from '@solid-ui-components/FlexContent'
import FlexOverlapFade from '@solid-ui-components/FlexOverlapFade'
import ContentImages from '@solid-ui-components/ContentImages'
import ContentButtons from '@solid-ui-components/ContentButtons'
import WithDefaultContent from '@solid-ui-blocks/WithDefaultContent'

const FeaturesWithPhotoBlock06 = ({
  content: {
    kicker,
    title,
    text_1,
    text_2,

    images,
    collection,
    buttons
  },
  reverse
}) => (
  <Container sx={{ position: `relative` }}>
    <Flex
      sx={{
        alignItems: [null, `center`],
        flexDirection: [
          reverse ? `column-reverse` : `column`,
          reverse ? `row-reverse` : `row`
        ],
        mx: [null, null, null, -4]
      }}
    >
      <FlexImage reverse={reverse}>
        <ContentImages content={{ images }} reverse={reverse} />
      </FlexImage>
      <FlexContent reverse={reverse}>
        <StackTitle
          kicker={kicker}
          title={title}
          lines={[text_1, text_2]}
          align={[`center`, `left`]}
          space={0}
        />
        {collection && (
          <>
            <Divider space={3} />
            {collection.map(({ kicker, title, description }, index) => (
              <Box key={`item-${index}`} mb='4'>
                <Flex
                  sx={{
                    justifyContent: `space-between`,
                    alignItems: `center`,
                    mb: 3,
                    px: 2
                  }}
                >
                  <Box>
                    <ContentText content={kicker} variant='h5' mb='2' />
                    <ContentText content={description} />
                  </Box>
                  <ContentText
                    content={title}
                    variant='h2'
                    sx={{ color: `omegaDarker` }}
                    mb='0'
                    pl='3'
                  >
                    <Counter from='0' to={title?.text} duration={2} />%
                  </ContentText>
                </Flex>
                <ProgressBar
                  color={title?.color || undefined}
                  from='0%'
                  to={title?.text}
                  duration={2}
                />
              </Box>
            ))}
          </>
        )}
        <ContentButtons content={buttons} space={3} />
      </FlexContent>
    </Flex>
    <FlexOverlapFade direction={reverse ? 'ltr' : 'rtl'} />
  </Container>
)

export default WithDefaultContent(FeaturesWithPhotoBlock06)
